<!--  -->
<template>
    <div class="example-app-1">
        <div class="text">{{ msg }};</div>
    </div>
</template>

<script>
    export default {
        name: 'example1',
        // 
        data: function () {
            return {
                lists: [
                    '',
                    ' iOS',
                    '2019830',
                    '',
                    '10',
                    '202036Vue',
                    ' 40  Vue',
                    '202037 LovueLove vue',
                    '',
                    '',
                    '',
                    ' Vue',
                    ''
                ],
                curIndex: 0
            };
        },
        // 
        mounted() {
            this.startRun();
        },
        // 
        computed: {
            msg: function() {
                return this.lists[this.curIndex];
            }
        },
        // 
        methods: {
            startRun() {
                setInterval(() => {
                    this.curIndex += 1;
                    if (this.curIndex >= this.lists.length) {
                        this.curIndex = 0;
                    }
                }, 3000);
            }
        }
    }
</script>

<style lang="less" scoped>
.example-app-1 {
    padding: 20px;
    .text {
        font-size: 20px;
        font-weight: 700;
    }
}
</style>