<template>
    <div>
        <pre v-if="code" :data-line='dataLine' :class="{'line-numbers':lineNumbers}">
            <code :class="codeClass" class="match-braces" v-text="code"></code>
        </pre>
    </div>
</template>

<script>
import Prism from "prismjs";
import "prismjs/themes/prism-tomorrow.css";
Prism.plugins.NormalizeWhitespace.setDefaults({
    // 
    "remove-trailing": true,
    // 
    "remove-indent": true,
    // 
    "left-trim": true,
    // 
    "right-trim": true,
    // 80
    "break-lines": 80
    // 'indent': 1, //
    // 'tabs-to-spaces': 4, //
    // 'spaces-to-tabs': 4 //
});
export default {
    name: "CodeHighLight",
    props: {
        // 
        code: {
            type: String,
            required: true
        },
        // JS
        lang: {
            type: String,
            default: "javascript"
        },
        // 
        lineNumbers: {
            type: Boolean,
            default: true
        },
        // 
        dataLine: {
            default: false
        },
        // rgba(0, 255, 255, 0.2)
        localCodeBgColor: {
            type: String
        }
    },
    data() {
        return {
            cssPrefix: "./themes/prism-",
            defaultTheme: "tomorrow",
            theme: "",
            themeList: {
                // "coy",
                dark: "Dark",
                // "funky",
                okaidia: "Okadia",
                solarizedlight: "Solarized Light",
                tomorrow: "Tomorrow Night",
                twilight: "Twilight"
            }
        };
    },
    methods: {
        highlightAll() {
            // 
            if (this.localCodeBgColor) {
                this.changeLocalCodeColor();
            }
            Prism.highlightAll();
            // console.log("highlight done.");
        },
        changeLocalCodeColor() {
            // 
            document.documentElement.style.setProperty(
                "--local-code-bgc",
                this.localCodeBgColor
            );
        },
        addCss(theme) {
            let link = document.createElement("link");
            let head = document.getElementsByTagName("head")[0];
            link.rel = "stylesheet";
            link.type = "text/css";
            link.href = this.cssPrefix + theme + ".css";
            head.appendChild(link);
        },
        loadCss(theme) {
            let cssLink = "link[href^='" + this.cssPrefix + "']";
            let link = document.head.querySelectorAll(cssLink);
            if (link.length) {
                // css
                for (let i = 0; i < link.length; i++)
                    link[i].href = this.cssPrefix + theme + ".css";
                return;
            }
            this.addCss(theme);
            // console.log("loadCss done.");
        }
    },
    computed: {
        codeClass: function() {
            return "language-" + this.lang;
        }
    },
    watch: {
        theme: function(newVal) {
            this.loadCss(newVal);
        }
    },
    mounted() {
        this.highlightAll();
    },
    updated() {
        this.highlightAll();
    }
};
</script>

<style>
:root {
    --local-code-bgc: rgba(0, 255, 255, 0.2);
}
</style>

<style scoped>
pre {
    border-radius: 0.5rem;
    max-height: 25rem;
    overflow: auto;
}

pre /deep/ .line-highlight {
    background: var(--local-code-bgc);
    margin-left: 3.5rem;
}

