::-webkit-scrollbar-track-piece {
    background-color: #f8f8f8;
}
::-webkit-scrollbar {
    width: 0.75rem;
    height: 0.75rem;
}
::-webkit-scrollbar-thumb {
    border-radius: 0.375rem;
    background-color: #dddddd;
    background-clip: padding-box;
    min-height: 2rem;
}
::-webkit-scrollbar-thumb:hover {
    background-color: #bbb;
}
</style>